import React, {Fragment, useEffect, useState} from 'react'

console.log('HooksDemo Outside')

/**
 * useDebounceカスタムフック
 * @param val
 * @param delay
 */
export const useDebounce = (val, delay) => {
  console.log("useDebounce Run");
  const [debouncedVal, setDebouncedVal] = useState(val)
  // useEffect()的作用就是指定一个副效应函数，组件每渲染一次，该函数就自动执行一次

  useEffect(() => {
    const timeout = setTimeout(() => {
      console.log('useDebounce.clearTimeout')
      setDebouncedVal(val)
    }, delay)
    // 每次在上一次useEffect处理完后运行return返回的函数
    // React 会在组件卸载的时候执行清除操作。正如之前学到的，effect 在每次渲染的时候都会执行。
    // 这就是为什么 React 会在执行当前 effect 之前对上一个 effect 进行清除
    return () => {
      console.log('useDebounce.clearTimeout')
      clearTimeout(timeout)
    }
  }, [val, delay])
  console.log('useDebounce.return')
  return debouncedVal
}

const useMyTest = (val) => {
  console.log('useMyTest...')
  return val
}

export default function HooksDemo() {
  // 只要执行setState，就会执行render函数，也就是下列代码
  console.log("HooksDemo Render")

  const [count, setCount] = useState(1)
  const inputRef = React.useRef()
  const [inputVal, setInputVal] = useState("")

  const debouncedVal = useDebounce(inputVal, 1000)
  // 每次执行Render，hook函数都会执行
  const hookVal = useMyTest(10)
  useEffect(() => {
    console.log('useEffect run....')
  }, [])
  console.log(hookVal)
  // useEffect(() => {
  //   const timer = setInterval(() => {
  //     // 第一种写法
  //     // setCount(count + 1)
  //     // 第二种写法
  //     setCount(count => count + 1)
  //   }, 1000)
  //   // return 的函数在卸载组件时执行
  //   return () => {
  //     console.log('useEffect.clearInterval')
  //     clearInterval(timer)
  //   }
  // }, []) // []表示不监测任何数据，[count]监测count，什么都不写表示监测所有数据

  const handleChange = (event) => {
    console.log('handleChange:', event.target.value)
    console.log('handleChange.inputRef:', inputRef.current)
    setInputVal(event.target.value)
    // setDebouncedVal(event.target.value)
  }


  const handleAddBtnClick = () => {
    setCount(count + 1)
  }
  return (
      <Fragment>
        HooksDemo Works
        <p>Count:{count}</p>
        <p>DebouncedVal:{debouncedVal}</p>
        输入框：<input type={"text"} ref={inputRef} onChange={handleChange}></input>
        <button onClick={handleAddBtnClick}>Add</button>
        <br/>
        Count:{count}
      </Fragment>
  )
}
